@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  src: url("../assets/fonts/Roboto-regular/Roboto-regular.eot");
  src: url("../assets/fonts/Roboto-regular/Roboto-regular.eot?#iefix")
      format("embedded-opentype"),
    local("Roboto"), local("Roboto-regular"),
    url("../fonts/Roboto-regular/Roboto-regular.woff2") format("woff2"),
    url("../fonts/Roboto-regular/Roboto-regular.woff") format("woff"),
    url("../fonts/Roboto-regular/Roboto-regular.ttf") format("truetype"),
    url("../assets/fonts/Roboto-regular/Roboto-regular.svg#Roboto")
      format("svg");
}
