body {
  color: #543462;
  font-family: 'Roboto', sans-serif;
  margin: 0; }

* {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, p {
  font-family: 'Roboto', sans-serif;
  margin: 0; }

a {
  color: #ffffff;
  text-decoration: none; }

.navbar-fixed-top {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 1em 0;
  position: fixed;
  top: 0;
  width: 100%;
  .logo {
    background-image: url('../../assets/images/imagotype.svg');
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: contain;
    display: block;
    height: 2.5em;
    width: 2.5em; }
  .page-scroll {
    font-weight: 200;
    margin: 0 1em; } }

.section {
  height: 35em;
  width: 100%; }
